

*{
    list-style: none;
    color: white;
    /* background-color:rgb(1, 1, 36) */
}
.border-custom {
   border: 2px solid rgb(1, 1, 36) !important;
 }
 

.column {
    float : left;
    width: 50%;
   
}

 .row:after{
content: "";
display: table;
clear: both;
 }
 
 
 .card{
text-align: center;
display: inline-block;
margin: 20px;
background-color:rgb(1, 1, 36);
max-width: 300px;
padding-inline: 20px;
box-shadow: 0 2px 0 rgba(0, 0, 0, 0.3)
 }

 .card button{
    border: none;
    outline: 0;
    display: inline-block;
    padding: 8px;
    color: white;
    background-color: #483d8b;
    text-align: center;
    cursor: pointer;
    width: 100%;
    transition: 0.3s;
    border-radius: 10px;
 }

 .card button:hover{
    opacity: 0.5;
 }

 .card img{
    border-radius: 15px;
    box-shadow: 0 3px 0 rgba(160, 160, 160, 0.6) 
    
 }

 body{
   text-align: center;
   font-size: 17px;
   padding: 2%;
   background-color:rgb(1, 1, 36);
   font-family: "DM Serif Display", serif;
   
 }

 .profile{
   width: 250px;
   height: 250px;
   border-radius:125px;
   margin-bottom: 60px;
   border-style:ridge;
   border-color: #483d8b;
 }

 .btn{
   background-color: #483d8b;
   color: white;
   padding: 12px 30px;
   text-align: center;
   font-size: 18px;
   margin: 4px 2px;
   opacity: 1;
   transition: 0.3s;
 }

 .btn:hover{
   opacity: 0.5;
   }
 
   /* nice trick in css , when you want to keep it as phone screen  */

   @media (max-width:800px) {
      .column{
         width: 250px;
      }
      
   }

   .title-fade-in{
      margin: 20px;
      padding-right: 4%;
      align-items: center;
      opacity: 0;
      animation-name: fade-in;
      animation-duration: 2s;

   }

   
   .title-fade-out{
      margin: 20px;
      padding-right: 4%;
      align-items: center;
      opacity: 100;
      animation-name: fade-out;
      animation-duration: 2s;
      
   }

   @keyframes fade-out {
      from{
         opacity: 100;
      }
      to {
  opacity: 0;
      }
   }


   @keyframes fade-in {
      from{
         opacity: 0;
      }
      to {
  opacity: 100;
      }
   }

   body {
      text-align: center;
      font-size: 17px;
      padding: 2%;
      background-color: rgb(1, 1, 36);
      /* rgb(1, 1, 36); */
      font-family: "DM Serif Display", serif;
      background-image: url('https://www.transparenttextures.com/patterns/stardust.png'); /* Stars effect */
      background-size: 1000px; /* Make stars bigger */
      animation: starsMove 20s linear infinite; /* Stars animation */
      overflow-x: hidden; /* Prevent horizontal scroll */
   }
   
   @keyframes starsMove {
      from {
         background-position: 0 0;
      }
      to {
         background-position: 1000px 1000px;
      }
   }

   body::after {
      content: "";
      position: absolute;
      width: 100%;
      height: 30%;
      background: rgba(255, 255, 255, 0.02);
      animation: twinkle 3s infinite alternate;
   }
   
   @keyframes twinkle {
      from {
         opacity: 0.1;
      }
      to {
         opacity: 0.4;
      }
   }

   
   .header {
      background-color: #483d8b;
      padding: 10px 20px;
      display: flex;
      justify-content: center;  /* Centers content horizontally */
      align-items: center;  /* Centers content vertically */
      box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
      border-radius: 10px;
      width: 100%; /* Makes sure it spans the full width */
    }
    
    .nav-container {
       
      display: flex;
      gap: 40px;  /* Adds space between the links */
    }
    
    .nav-link {
      color: white;
      text-decoration: none;
      font-size: 20px;
      font-weight: 600;
      padding: 8px 16px;
      border-radius: 5px;
      transition: background-color 0.3s ease-in-out, transform 0.2s;
    }
    
    .nav-link:hover {
      background-color: #6a5acd;
      transform: scale(1.1);
    }
    
   .gallery{
    display: inline-block;
    margin: 15px;
    width: 300px;
   }
   
   .gallery img:hover{
      filter: brightness(50%);
   }

   .gallery img {
      width:100%;
      height: auto;
   }

   .gallery p{
      text-align: center;
      font-size: 16px;
      padding: 10px;
   }